<template>
  <div class="sticky">
    <van-sticky>
      <van-tabs v-model="active" title-active-color="#3386e2" color="#3386e2">
        <van-tab :to="{ path: '/main/applist'}">
          <div slot="title">
            软件榜
          </div>
        </van-tab>
        <van-tab title="游戏榜单" :to="{ path: '/main/GameList'}">
          <div slot="title">
            游戏榜
          </div>
        </van-tab>
      </van-tabs>
    </van-sticky>
  </div>
</template>

<script>
import { Sticky, Tab, Tabs  } from "vant";
export default {
  props: {
    choice: {
      type: Number,
      default: 0
    }
  },
  components: {
    [Sticky.name]: Sticky,
    [Tabs.name]: Tabs,
    [Tab.name]: Tab
  },
  data() {
    return {
      active: 0,
    };
  },
  mounted() {
    this.active = this.choice;
  },
  methods: {}
};
</script>

<style lang="less">
.sticky {
    .van-tabs__nav {
        background: #f9f9f9;
        .van-ellipsis {
            font-size: 15.97px;
        }
    }
}
</style>
