<template>
  <div class="playList">
    <div
      v-for="(item,index) in content"
      class="appList"
      :key="index"
      :style="{'borderBottom': index==content.length-1 ? '1px solid #fff':'1px solid #E0E0E0'}"
    >
      <router-link :to="{ path: '/appDetails', query: { contentid: item.contentid}}">
        <div class="IconImg">
          <img :src="item.iconurl" alt />
        </div>
        <div class="AppIntro">
          <span class="appName" v-html="item.name"></span>
          <span class="appFlow" v-html="(item.appsize/1024).toFixed(2)+'M'"></span>
          <span class="downloads" v-html="item.interested"></span>
        </div>
      </router-link>
      <div class="buttonGroup">
        <van-button class="downButton" :url="item.orderurl" round plain type="info" size="small">下载</van-button>
      </div>
    </div>
    <div class="separate"></div>
  </div>
</template>

<script>
import { Button } from "vant";
import axios from "axios";
import horizontal from "./horizontal";
import videoCard from "./videoCard";
export default {
  components: {
    horizontal,
    videoCard,
    [Button.name]: Button
  },
  computed: {
  },
  data() {
    return {
      active: 0,
      list: [],
      loading: false,
      finished: false,
      propTitle: true,
      idx: 0,
      detailurl: ""
    };
  },
  props: ["content"],
  //内容，标题，单元格是否有下边框
  mounted() {},
  methods: {
    onLoad() {
      // 异步更新数据
      setTimeout(() => {
        // for (let i = 0; i <2; i++) {
        // this.list==t;
        // }
        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.content.length >= 3) {
          this.finished = true;
        }
      }, 500);
    }
  }
};
</script>

<style lang="less">
.playList {
  overflow: hidden;
  .borderBottom {
    border-bottom: none;
  }
  .van-swipe-item {
    img {
      width: 100%;
      height: 140px;
    }
  }
  .van-tabbar--fixed {
    position: relative;
  }
}
.appList {
  padding-top: 14px;
  padding-bottom: 14px;
  .IconImg {
    float: left;
    width: 59px;
    height: 59px;
    margin: 0 13.88px;
    overflow: hidden;
    border-radius: 9.72px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .AppIntro {
    display: inline-block;
    .appName {
      display: block;
      margin-bottom: 5.2px;
      font-size: 16.6px;
      font-weight: bold;
      color: #212121;
      width: 200px;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
    }
    .appFlow {
      display: block;
      margin-bottom: 3.81px;
      color: #b8b8b8;
      font-size: 12px;
    }
    .downloads {
      display: block;
      font-size: 11.11px;
      color: #bdbdbd;
    }
  }
  .buttonGroup {
    float: right;
    height: 55.55px;
    margin-right: 13.88px;
    box-sizing: border-box;
    line-height: 55.55px;
    .downButton {
      float: none;
      overflow: hidden;
      margin-bottom: 3px;
    }
  }
}
.van-list {
  overflow: hidden;
}
</style>
